import React, { Component } from "react";
import { Box, MotionDiv, Text, HeaderLink } from "../styles";
import { AnimatePresence } from "framer-motion";
import OutsideClickHandler from "react-outside-click-handler";
import HamburgerMenu from "../components/HamburgerMenu";
import Portal from "../lib/Portal";

export default class HeaderModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
      disabled: true,
    };
  }

  render() {
    return (
      <Box>
        <Box
          zIndex={2}
          onClick={() => {
            this.setState((state) => ({ isOpen: true }));
          }}
        >
          <HamburgerMenu isOpen={this.state.isOpen} />
        </Box>

        <Portal targetId="movepage">
          <AnimatePresence>
            {this.state.isOpen && (
              <MotionDiv
                key="modal"
                initial={{ backgroundColor: "rgba(0, 0, 0, 0)", y: 80 }}
                animate={{ backgroundColor: "rgba(0, 0, 0, 0.1)" }}
                exit={{ backgroundColor: "rgba(0, 0, 0, 0)" }}
                width="100%"
                height="100%"
                position="absolute"
                top={0}
                left={0}
                transition={{ duration: 0.2 }}
              >
                <OutsideClickHandler
                  onOutsideClick={() => {
                    setTimeout(() => {
                      this.setState({ isOpen: false });
                    }, 0);
                  }}
                >
                  <MotionDiv
                    transition={{ duration: 0.2 }}
                    initial={{ height: 0, y: 0 }}
                    animate={{ height: 220 }}
                    exit={{ height: 0 }}
                    width="100vw"
                    pl={30}
                    backgroundColor="white"
                    className="flex justify-start flex-column overflow-hidden"
                    pt={0}
                  >
                    <Box className="flex flex-column">
                      <HeaderLink to={{ pathname: "/" }} exact={true} mb={20}>
                        <Text
                          fontFamily="TsukuARdGothicBold"
                          fontSize={16}
                          color="grey5"
                        >
                          ホーム
                        </Text>
                        <Text
                          pl={4}
                          color="grey5"
                          fontSize={12}
                          fontFamily="TsukuARdGothicBold"
                        >
                          Home
                        </Text>
                      </HeaderLink>
                      <HeaderLink to={{ pathname: "/reservation" }} mb={20}>
                        <Text
                          fontFamily="TsukuARdGothicBold"
                          fontSize={16}
                          color="grey5"
                        >
                          撮影のご予約
                        </Text>
                        <Text
                          pl={4}
                          color="grey5"
                          fontSize={12}
                          fontFamily="TsukuARdGothicBold"
                        >
                          Reservation
                        </Text>
                      </HeaderLink>
                      <HeaderLink to={{ pathname: "/contact" }} mb={20}>
                        <Text
                          fontFamily="TsukuARdGothicBold"
                          fontSize={16}
                          color="grey5"
                        >
                          お問い合わせ
                        </Text>
                        <Text
                          pl={4}
                          color="grey5"
                          fontSize={12}
                          fontFamily="TsukuARdGothicBold"
                        >
                          Contact
                        </Text>
                      </HeaderLink>
                      <HeaderLink to={{ pathname: "/profile" }} mb={20}>
                        <Text
                          fontFamily="TsukuARdGothicBold"
                          fontSize={16}
                          color="grey5"
                        >
                          プロフィール
                        </Text>
                        <Text
                          pl={4}
                          color="grey5"
                          fontSize={12}
                          fontFamily="TsukuARdGothicBold"
                        >
                          Profile
                        </Text>
                      </HeaderLink>
                    </Box>
                  </MotionDiv>
                </OutsideClickHandler>
              </MotionDiv>
            )}
          </AnimatePresence>
        </Portal>
      </Box>
    );
  }
}
