import React from "react";
import { Box, Clickable, Text } from "../styles";

export function Btn(props) {
  return (
    <Clickable
      height={50}
      className="border-box pointer"
      display="flex"
      alignItems="center"
      justifyContent="center"
      backgroundColor="#4d4646"
      width={props.width}
      {...props}
    >
      <Text
        px={40}
        height="auto"
        fontSize={props.fontSize === undefined ? 18 : props.fontSize}
        color="white"
        fontFamily="TsukuARdGothic"
        className="flex items-center justify-center border-box"
        height={50}
      >
        {props.children}
      </Text>
    </Clickable>
  );
}

