import React, { Component } from "react";
import Page1 from "./Page1";
import Page2 from "./Page2";
import Page3 from "./Page3";
import { MotionDiv, Box, MovePage } from "../styles";
import Header from "../components/Header";
import Footer from "../components/Footer";

import { BrowserRouter as Router, useRouteMatch } from "react-router-dom";
import {
  useStateWithLocalStorage,
  useStateObjectWithLocalStorage,
} from "../lib/useStateWithLocalStorage";
import { AnimatePresence } from "framer-motion";

export default function Reservation(props) {
  const [page, setPage] = useStateWithLocalStorage("page", 1);
  const [prefecture, setPrefecture] = useStateWithLocalStorage(
    "prefecture",
    ""
  );
  const [address, setAddress] = useStateWithLocalStorage("address", "");
  const [mail, setMail] = useStateWithLocalStorage("mail", "");
  const [phonenumber, setPhonenumber] = useStateWithLocalStorage(
    "phonenumber",
    ""
  );
  const [request, setRequest] = useStateWithLocalStorage("request", "");
  const [menu, setMenu] = useStateWithLocalStorage("menu", "");
  const [category, setCategory] = useStateWithLocalStorage("category", "");
  const [hour, setHour] = useStateWithLocalStorage("hour", "");
  const [year, setYear] = useStateWithLocalStorage("year", "");
  const [month, setMonth] = useStateWithLocalStorage("month", "");
  const [day, setDay] = useStateWithLocalStorage("day", "");
  const [number, setNumber] = useStateWithLocalStorage("number", "30枚");
  const [type, setType] = useStateObjectWithLocalStorage("type", ["USB"]);
  const setStateList = {
    page: setPage,
    prefecture: setPrefecture,
    address: setAddress,
    mail: setMail,
    phonenumber: setPhonenumber,
    request: setRequest,
    menu: setMenu,
    category: setCategory,
    hour: setHour,
    year: setYear,
    month: setMonth,
    day: setDay,
    number: setNumber,
    type: setType,
  };
  const onChange = function (event) {
    if (event.target === undefined) {
      setStateList[event.name](event.value);
    } else {
      setStateList[event.target.name](event.target.value);
    }
  };

  let { path, url } = useRouteMatch();
  return (
    <MovePage id="movepage">
      <Header />
      <AnimatePresence exitBeforeEnter initial={false}>
        {useRouteMatch(`${url}`).isExact && (
          <MovePage>
            <Page1
              category={category}
              menu={menu}
              number={number}
              month={month}
              day={day}
              hour={hour}
              type={type}
              prefecture={prefecture}
              address={address}
              phonenumber={phonenumber}
              mail={mail}
              request={request}
              onChange={onChange}
              url={url}
            />
          </MovePage>
        )}
      </AnimatePresence>
      <AnimatePresence exitBeforeEnter initial={false}>
        {useRouteMatch(`${url}/confirmation`) && (
          <MovePage>
            <Page2
              category={category}
              menu={menu}
              number={number}
              month={month}
              day={day}
              hour={hour}
              type={type}
              prefecture={prefecture}
              address={address}
              phonenumber={phonenumber}
              mail={mail}
              request={request}
              onChange={onChange}
              url={url}
              history={props.history}
            />
          </MovePage>
        )}
      </AnimatePresence>

      <AnimatePresence exitBeforeEnter initial={false}>
        {useRouteMatch(`${url}/success`) && (
          <MovePage>
            <Page3 />
          </MovePage>
        )}
      </AnimatePresence>
      <Footer />
    </MovePage>
  );
}
