// Customize this 'myform.js' script and add it to your JS bundle.
// Then import it with 'import MyForm from "./myform.js"'.
// Finally, add a <MyForm/> element whereever you wish to display the form.

import React from "react";
import { Box } from "../styles";
export default class MyForm extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: "",
    };
  }

  render() {
    const { status } = this.state;

    return (
      <form
        onSubmit={this.submitForm}
        action="https://formspree.io/f/mgepawdl"
        method="POST"
      >
        <input
          className="display-none"
          type="text"
          name="category"
          defaultValue={this.props.category}
        />
        <input
          className="display-none"
          type="text"
          name="menu"
          defaultValue={this.props.menu}
        />
        <input
          className="display-none"
          type="text"
          name="number"
          defaultValue={this.props.number}
        />
        <input
          className="display-none"
          type="text"
          name="month"
          defaultValue={
            String(this.props.month) +
            "月" +
            String(this.props.day) +
            "日" +
            String(this.props.hour) +
            "時"
          }
        />
        <input
          className="display-none"
          type="text"
          name="type"
          defaultValue={String(this.props.type)}
        />
        <input
          className="display-none"
          type="text"
          name="prefecture"
          defaultValue={this.props.prefecture}
        />
        <input
          className="display-none"
          type="text"
          name="address"
          defaultValue={this.props.address}
        />
        <input
          className="display-none"
          type="text"
          name="phonenumber"
          defaultValue={this.props.phonenumber}
        />
        <input
          className="display-none"
          type="text"
          name="mail"
          defaultValue={this.props.mail}
        />
        <input
          className="display-none"
          type="text"
          name="request"
          defaultValue={this.props.request}
        />
        {this.props.children}
        {status === "ERROR" && <p>Ooops! There was an error.</p>}
      </form>
    );
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.props.history.push("./success");
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }
}
