import React, { Component, findDOMNode } from "react";
import { Box, Text, MotionDiv } from "../styles";
import { ArrowSvg } from "../styles";
import styled from "styled-components";

function onTapCancel(event, info) {
  console.log(info.point.x, info.point.y);
}
class Dropdown extends Component {
  constructor(props) {
    super(props);
    this.state = {
      toggle: false,
      up: false,
    };
    this.changeToggle = this.changeToggle.bind(this);
    this.closeToggle = this.closeToggle.bind(this);
    this.myRef = React.createRef();
  }

  componentDidMount() {
    document.addEventListener("click", this.closeToggle);
  }
  componentWillUnmount() {
    document.removeEventListener("click", this.closeToggle);
  }

  checkPosition() {
    if (this.myRef.current !== null) {
      const result =
        this.myRef.current.getBoundingClientRect().top +
          this.myRef.current.getBoundingClientRect().height / 2 <
        window.innerHeight / 2;
      this.setState({ up: result });
    }
  }

  closeToggle(e) {
    if (!this.myRef.current.contains(e.target)) {
      this.setState((state) => {
        return { toggle: false };
      });
    }
  }
  changeToggle() {
    this.checkPosition();
    this.setState((state) => {
      return { toggle: !state.toggle };
    });
  }

  render() {
    let title = "";
    if (this.props.overrideTitle) {
      if (this.props.value !== "") {
        title = "";
      } else {
        title = this.props.title;
      }
    } else {
      title = this.props.title;
    }
    return (
      <MotionDiv
        ref={this.myRef}
        onTapCancel={onTapCancel}
        onClick={this.changeToggle}
        className="flex relative border-box user-select"
        style={{
          cursor: "pointer",
          outline: this.state.toggle ? "1px solid #333" : "none",
        }}
        width="100%"
        height={this.state.toggle ? "calc(100% - 10px)" : "100%"}
        p={20}
      >
        <Text fontFamily="TsukuARdGothic" fontSize={14} color="grey7">
          <Text className="inline" fontSize={16} mr={4}>
            {this.props.value}
          </Text>
          {title}
        </Text>
        <MotionDiv
          ml="auto"
          variants={{
            active: {
              rotate: 0,
            },
            inactive: {
              rotate: 180,
            },
          }}
          transition={{ type: "linear", duration: 0 }}
          originY={0.6}
          animate={this.state.toggle ? "active" : "inactive"}
        >
          <ArrowSvg />
        </MotionDiv>

        <MotionDiv
          initial={{
            opacity: 26,
            x: -1,
            display: "none",
          }}
          variants={{
            active: {
              display: "block",
              opacity: 1,
            },
            inactive: {
              opacity: 0,
              transitionEnd: {
                display: "none",
              },
            },
          }}
          transition={{ duration: 0.1 }}
          animate={this.state.toggle ? "active" : "inactive"}
          className="absolute overflow-scroll"
          height={200}
          width="100%"
          backgroundColor="white"
          border="solid 1px #cecece"
          top={this.state.up ? 57 : -223}
          left={0}
          borderTop={!this.state.up ? "solid 1px #cecece" : "none"}
          borderBottom={this.state.up ? "solid 1px #cecece" : "none"}
          py={10}
          zIndex={1}
        >
          {this.props.label.map((value) => (
            <TextBox
              key={value}
              onClick={() =>
                this.props.onChange({
                  name: this.props.name,
                  value: value,
                })
              }
            >
              <Text fontFamily="TsukuARdGothic" fontSize={14} color="black">
                {value}
              </Text>
            </TextBox>
          ))}
        </MotionDiv>

        {/* <button onClick={() => setActive(!toggle)}>Click me</button> */}
      </MotionDiv>
    );
  }
}

Dropdown.defaultProps = {
  overrideTitle: false,
};

export default Dropdown;

const TextBox = styled.div`
  box-sizing: border-box;
  height: 40px;
  margin: 0 10px;
  background-color: white;
  padding: 10px 10px;
  transition: 0.2s;
  &: hover {
    transition: 0.2s;
    background-color: #e9e9e9;
  }
`;
