import styled from "styled-components";
import { motion } from "framer-motion";
import {
  space,
  color,
  typography,
  layout,
  flexbox,
  background,
  border,
  position,
  shadow,
} from "styled-system";

export const MotionDiv = styled(motion.div)`
  ${space};
  ${color};
  ${typography};
  ${layout};
  ${flexbox};
  ${background};
  ${border};
  ${position};
  ${shadow};
`;

export function MovePage(props) {
  return (
    <MotionDiv
      onAnimationComplete={() =>
        props.prevent == null ? window.scrollTo({ top: 0, left: 0 }) : null
      }
      animate={{
        x: 0,
        opacity: 1,
        transition: { duration: 0.5, ease: "easeIn" },
      }}
      initial={{
        x: 30,
        opacity: 0,
        transition: { duration: 0.5, ease: "easeIn" },
      }}
      exit={{
        opacity: 0,
        transition: { duration: 0.15, ease: "easeOut" },
      }}
      {...props}
    >
      {props.children}
    </MotionDiv>
  );
}
