import { RightArrow, LeftArrow, Box, Text, MotionDiv } from "../styles";

function SampleNextArrow(props) {
  const { className, style, onClick } = props;
  return (
    <RightArrow
      className={className}
      width={48}
      height={48}
      style={{
        ...style,
        display: "block",
        right: "14%",
        zIndex: 1,
      }}
      onClick={onClick}
    />
  );
}

function SamplePrevArrow(props) {
  const { className, style, onClick } = props;
  return (
    <LeftArrow
      className={className}
      width={48}
      height={48}
      style={{
        ...style,
        display: "block",
        left: "14%",
        zIndex: 1,
      }}
      onClick={onClick}
    />
  );
}

