const settings = {
  dots: false,
  infinite: true,
  speed: 500,
  slidesToShow: 2,
  slidesToScroll: 1,
  centerPadding: "16%",
  centerMode: true,
  nextArrow: <SampleNextArrow />,
  prevArrow: <SamplePrevArrow />,
  waitForAnimate: false,
  responsive: [
    {
      breakpoint: 1080,
      settings: {
        slidesToShow: 2,
      },
    },
    {
      breakpoint: 640,
      settings: {
        slidesToShow: 1,
      },
    },
  ],
};

export default class SimpleSlider extends Component {
  constructor(props) {
    super(props);
    this.state = {
      category: "wedding",
    };
  }

  render() {
    return (
      <Box>
        <Box
          className="flex border-box"
          justifyContent={["center", "left", "left"]}
          px={[10, 80, 80]}
        >
          <AnimateSharedLayout>
            {[
              ["wedding", "フォト・ウエディング"],
              ["outdoor", "イベント・アウトドア"],
              ["person", "人物撮影"],
              ["else", "その他"],
            ].map((item, index) => {
              const isSelected = item[0] === this.state.category;
              return (
                <Item
                  key={index}
                  className="pointer border-box"
                  mr={["4%", 20, 20]}
                  mb={20}
                  pb={4}
                  fontFamily="TsukuARdGothicBold"
                  fontSize={["calc(12px + 6%)", 16, 16]}
                  color={isSelected ? "black" : "grey7"}
                  onClick={() => this.setState({ category: item[0] })}
                  isSelected={isSelected}
                >
                  {format(item[1])}
                </Item>
              );
            })}
          </AnimateSharedLayout>
        </Box>
        <AnimatePresence>
          <Slider ref={(c) => (this.slider = c)} {...settings}>
            {urls[this.state.category].map((url, index) => {
              return (
                <MotionDiv
                  animate={{
                    opacity: 1,
                  }}
                  initial={{
                    opacity: 0,
                  }}
                  exit={{
                    opacity: 0,
                  }}
                  transition={{ duration: 0.5 }}
                  key={index}
                >
                  <ImageBox className="relative" url={url}>
                    <Box
                      py={14}
                      px={20}
                      backgroundColor="white"
                      className="inline absolute"
                      bottom={6}
                      left={0}
                      display={["none", "display", "display"]}
                    >
                      <Text
                        fontSize={[12, 14]}
                        color="grey8"
                        fontFamily="TsukuARdGothic"
                        className="inline"
                      >
                        {this.state.category + "-" + String(index + 1)}
                      </Text>
                    </Box>
                  </ImageBox>
                </MotionDiv>
              );
            })}
          </Slider>
        </AnimatePresence>
      </Box>
    );
  }
}

function format(item) {
  return (
    <Box>
      {item.split("・").map((value) => {
        return (
          <span>
            {value} <Box display={["block", "none", "none"]}></Box>{" "}
          </span>
        );
      })}
    </Box>
  );
}

function Item(props) {
  return (
    <Box className="item" onClick={props.onClick} {...props}>
      {props.children}
      {props.isSelected && (
        <MotionDiv
          mt={4}
          layoutId="underline"
          initial={false}
          animate={{ border: "solid 1px #212529" }}
          transition={{
            type: "spring",
            stiffness: 500,
            damping: 30,
          }}
        ></MotionDiv>
      )}
    </Box>
  );
}

const ImageBox = styled.div`
  width: 95%;
  &:before {
    content: "";
    display: block;
    padding-top: 125%;
  }
  margin-left: 10px;
  margin-right: 10px;
  background-position: center;
  background-size: cover;
  background-image: ${(props) => "url(" + props.url + ")"};
`;
