import styled from "styled-components";
import { typography, space, layout, border, shadow } from "styled-system";

export const Text = styled.div`
  ${typography};
  ${space};
  ${layout};
  ${border};
  color: ${(props) => props.theme.colors[props.color]};

  text-shadow: ${(props) => {
    if (props.textShadow !== undefined) {
      return (
        String(props.textShadow[0]) +
        "px " +
        String(props.textShadow[0]) +
        "px 0 " +
        props.theme.colors[props.textShadow[1]]
      );
    } else {
      return "none";
    }
  }};
`;
