import React, { Component } from "react";
import { AnimatePresence } from "framer-motion";
import OutsideClickHandler from "react-outside-click-handler";
import { Box, MotionDiv, ModalInput, ModalTextArea, Form } from "../styles";
import { BoxBtn, WhiteBoxBtn } from "../components/Btn";
import Portal from "../lib/Portal";

export default class MailModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: props.isOpen,
    };
  }

  render() {
    return (
      <Box>
        <Portal targetId="root">
          <AnimatePresence>
            {this.state.isOpen && (
              <MotionDiv
                key="modal"
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
                width="100%"
                height="100%"
                position="fixed"
                top={0}
                left={0}
                backgroundColor="rgba(0, 0, 0, 0.4)"
                className="flex items-center justify-center"
              >
                <OutsideClickHandler
                  onOutsideClick={() => this.setState({ isOpen: false })}
                >
                  <MotionDiv
                    position="relative"
                    transition={{ duration: 0.3 }}
                    initial={{ y: -40 }}
                    animate={{ y: 0 }}
                    exit={{ y: -40 }}
                    minWidth={320}
                    maxWidth={600}
                    mx={20}
                    backgroundColor="white"
                    className="flex items-center justify-center"
                  >
                    <Form
                      p={10}
                      action="https://formspree.io/f/mgepawdl"
                      method="POST"
                      className="kwes-form"
                    >
                      <Box px={14}>
                        <ModalTextArea
                          mt={20}
                          cols={60}
                          name="message"
                          placeholder="ここにメッセージを入力してください。"
                          color="softBlack"
                        ></ModalTextArea>
                      </Box>
                      <ModalInput
                        placeholder="メールアドレス"
                        type="email"
                        name="email"
                      />

                      <Box
                        my={20}
                        borderBottom="solid 1px #E3E5ED"
                        width="100%"
                      ></Box>
                      <Box m={14} className="flex flex-end">
                        <WhiteBoxBtn
                          height={40}
                          fontSize={14}
                          ml="auto"
                          onClick={() => this.setState({ isOpen: false })}
                        >
                          キャンセル
                        </WhiteBoxBtn>
                        <BoxBtn
                          as="button"
                          height={40}
                          fontSize={14}
                          type="submit"
                        >
                          送信
                        </BoxBtn>
                      </Box>
                    </Form>
                  </MotionDiv>
                </OutsideClickHandler>
              </MotionDiv>
            )}
          </AnimatePresence>
        </Portal>

        <Box onClick={() => this.setState({ isOpen: true })}>
          {this.props.children}
        </Box>
      </Box>
    );
  }
}
